.background-app {
}
.App {
  text-align: center;
  display: flex;
  flex-grow: 2;
  // z-index: 999;

  &::before {
    content: "";
    position: fixed;
    // left: 0;
    // right: 0;
    display: block;
    background-image: url("https://images.unsplash.com/photo-1485470733090-0aae1788d5af?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=917&q=80");
    background-size: cover;
    filter: brightness(90%);
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  /* flex: 2 5 0; */
}

routes {
  color: #61dafb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
